.App {
  font-family: sans-serif;
  text-align: center;
}

body {
  height: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
  overflow: hidden;
}
.header {
  height: 68px;
  width: 100%;
  background-color: rgba(230, 230, 230, 0.9);
  display: inline-flex;
}

.footer {
  height: 70px;
  width: 100%;
  background-color: white;
}
.buttonClass {
  background-color: rgb(4, 69, 175);
  color: white;
  border: 0;

  height: 40px;
  padding: 0 13px;
}
button:hover {
  background-color: rgb(42, 97, 187);
}
.footerButtons {
  height: 32px;
  width: 35px;
}
.OKbutton {
  background-color: rgb(4, 69, 175);
  box-shadow: rgb(0 0 0 / 10%) 0px 3px 12px 0px;
  border-radius: 4px;
}
.OKbutton:hover {
  background-color: rgb(42, 97, 187);
}

.formcontainer {
  background-color: white;
  height: calc(100vh - 136px);
  width: 100%;
  display: flex;
  /* justify-content: center;
  justify-items: center; */
}
.group {
  height: 100%;
  width: 100%;
  display: inline-flex;
  justify-content: center;
  padding-top: 30vh;
}
.leftofleftchild {
  width: 30%;
}
.rightofrightchild {
  width: 10%;
}
.leftchild {
  width: 10vw;
  padding-right: 20px;
}
.optionlabel {
  padding-left: max(90%, 10px);
}
.rightchild {
  width: calc(100vw - 45%);
  margin-left: 5px;
}
.leftchild .option {
  /* align-self: right; */
  padding-top: 6px;
  padding-right: 20px;
  width: 100%;
  font-family: "Source Sans Pro", sans-serif;
  color: rgb(42, 97, 187);
}
.labelname {
  width: 100%;
  font-size: 24px;
  color: black;
  font-family: "Source Sans Pro", sans-serif;
  line-height: 32px;
  letter-spacing: 0.01em;
}
.group .inputspace .input-style {
  caret-color: rgb(42, 97, 187);
  width: 100%;
  box-shadow: none;
  border: none;
  outline: none;
  padding: 5px;
  font-size: 30px;
  line-height: 32px;
  color: rgb(42, 97, 187);
  margin-top: 5vh;
  border-bottom: 1px solid rgb(135, 167, 219);
  padding-bottom: 10px;
  font-family: "Nunito Sans", sans-serif;
  font-style: light;
  font-weight: 100;
}
.input-style::placeholder {
  color: rgb(42, 97, 187);
  opacity: 0.4;
  font-size: 32px;
  font-style: normal;
  letter-spacing: 0.01em;
  /* font-family: 'Trebuchet MS', sans-serif; */
  font-family: "Mukta Malar", sans-serif;
}

.group input[type="text"]:focus {
  border-bottom: 3px solid rgb(42, 97, 187);
}

.buttongroup {
  width: 100%;
  padding-top: 15px;
  display: inline-flex;
  align-items: center;
}
form {
  width: 80%;
}
.buttonspan {
  font-size: 20px;
  font-weight: 400;
  font-style: bold;
  padding-right: 5px;
  line-height: 24px;
  font-family: "Hind Siliguri", sans-serif;
}

.bi-check2 {
  line-height: 24px;
  font-size: 20px;

  font-weight: bolder !important;
}
.NextToOkspan {
  font-family: "Open Sans", sans-serif;
  font-size: 12px;
  font-style: bold;
  padding-left: 8px;
}
.spanbolder {
  font-style: bolder;
  font-weight: bolder;
}

.headerright {
  font-size: 25px;
  color: black;
  font-family: "Catamaran", sans-serif;
  line-height: 32px;
  letter-spacing: 0.01em;
  width: 60%;
  display: inline-flex;
  justify-content: left;
  padding-top: 24px;
  padding-left: max(10px, 1vh);
  align-items: left;
}
.leftofleftheader {
  width: 29%;

  height: 100%;
}
.headerleft {
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  line-height: 24px;
  letter-spacing: 0.01em;
  width: auto;
  display: inline-flex;
  justify-content: left;
  color: rgb(42, 97, 187);
  padding-top: 24px;
  float: left;
  margin: 0;
}
.bi-arrow-right-short {
  color: rgb(42, 97, 187);
  font-weight: bolder;
  width: 20px;
  display: inline-flex;
  font-style: bolder;
  font-size: 22px;
  padding-top: 25px;
  -webkit-text-stroke: 0.5px;
}
.typeformButton {
  float: right;
  height: 32px;
  border-radius: 4px;
}
.poweredBybutton {
  font-size: 14px;
}

.upbutton {
  margin-right: 0;
  margin-left: 95px;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}
.upbutton:hover {
  background-color: rgb(42, 97, 187);
}
.spacebetweenupdownbutton {
  width: 0.9px;
}

.downbutton {
  margin-right: 14px;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  border-top-left-radius: 1px;
  border-bottom-left-radius: 1px;
  margin-left: 0;
  padding-left: 11px;
  padding-right: 13px;
}
.downbutton:hover {
  background-color: rgb(42, 97, 187);
}
.footerfloatright {
  padding-top: 10px;
  float: right;
  display: flex;
}
.floatrighttypeformbutton {
  padding-top: 10px;
  float: right;
  margin-right: 10px;
}
.upDownClass {
  display: inline-flex;
}
.bi-chevron-up,
.bi-chevron-down {
  -webkit-text-stroke: 1px;
}
@media only screen and (max-width: 769px) {
  .formcontainer {
    width: 100vw;
  }
  .leftchild {
    width: 5px;
  }
  .rightchild {
    width: 100vw;
  }
  .leftofleftchild {
    width: 10px;
  }
  .rightofrightchild {
    width: 0px;
  }
  .leftofleftheader {
    width: 15px;
  }
  .headerright {
    font-size: 20px;
  }
  .labelname {
    font-size: 22px;
  }
  .inputspace .input-style::placeholder {
    font-size: 24px;
  }
  .upDownClass {
    float: left;
  }
  .footerfloatright {
    float: left;
    margin-left: -80px;
  }
}
